export default {
	data() {
		return {
			schoolId: 'S000099100001',
			dataRow: [],
			modalName: null,
			modalContent: '',
			yhname: '',
			start: '',
			end: '',
			now: '',
			isCurrent: false,
			optionscascader: [],
			multiArray: [[], []],
			multiIndex: [0, 0],
			objectArr: [],
			leftArr: [],
			toast: '请选择地点',
			checked: 0,
			
			wait: 0
		};
	},
	methods: {
		showDetail(e) {
			console.log(e);
			if (e.currentTarget.dataset.info.isSafe == 'True' && e.currentTarget.dataset.info.isCheck == 'True') {
				// this.showModel('此处不存在隐患!')
				uni.navigateTo({
					url: 'fireDetail/fireDetail?placeNo='+e.currentTarget.dataset.info.placeNo
				})
			} else if (e.currentTarget.dataset.info.isCheck == 'False') {
				this.showModel('该地点暂未检查！')
			}else if (e.currentTarget.dataset.info.isSafe == 'False' && e.currentTarget.dataset.info.isCheck == 'True'){
				uni.navigateTo({
					url: 'fireDetail/fireDetail?placeNo='+e.currentTarget.dataset.info.placeNo
				})
				
			}
		},
		MultiChange(e) {
			this.multiIndex = e.detail.value;
			console.log(this.multiIndex)
			this.toast = this.multiArray[0][this.multiIndex[0]]+ '——' +this.multiArray[1][this.multiIndex[1]];
			this.getXFdata(this.multiIndex[0],this.multiIndex[1])
		},
		MultiColumnChange(e) {
			console.log('修改的列为', e.detail.column, '，值为', e.detail.value);
			// this.multiArray = [this.leftArr,[1,2,3]]
			this.multiIndex[e.detail.column] = e.detail.value;
			var number = this.multiIndex[0];
			if (e.detail.column == 0) {
				var arr = [];
				for (let foo of this.optionscascader[number].ChildYS) {
					arr.push(foo.Name)
				}
				this.multiArray = [this.leftArr,arr];
				this.multiIndex[1] = 0;
			}
		},
		showModel(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {
					}
				}
			});
		},
		getXFPlace() {
			var self = this;
			uni
				.request({
					url: self.$store.state.urlPrefix + '/web/Get_XFPlacesByDepth3',
					data: {
						campusplaceNo: self.schoolId
					}
				})
				.then(res => {
					var [error, res] = res;
					// console.log(res.data);
					self.wait = res.data.data.wait || 0;
					self.checked = res.data.data.checked || 0;
					self.start = res.data.data.startTime;
					self.end = res.data.data.endTime || '暂未结束';
					self.optionscascader = res.data.data.detail;
					for (let foo of res.data.data.detail) {
						self.multiArray[0].push(foo.Name);
						self.leftArr.push(foo.Name);
					}
					for (let fooo of res.data.data.detail[0].ChildYS) {
						self.multiArray[1].push(fooo.Name);
					}
				});
		},
		getXFdata(a,b) {
			uni.showLoading({
				title:'查询中。。。',
				mask:true
			})
			console.log(a,b);
			console.log(this.optionscascader[a].ChildYS[b].placeNo)
			
			var self = this;
			self.dataRow = [];
			uni
				.request({
					url: self.$store.state.urlPrefix + '/web/Get_PlacesByNo',
					data: {
						campusplaceNo: self.schoolId,
						type: 'XF',
						placeNo: this.optionscascader[a].ChildYS[b].placeNo
					}
				})
				.then(res => {
					var [error, res] = res;
					uni.hideLoading()
					if (res.data.code == 0) {
						
						for (let site of res.data.data) {
							self.dataRow.push(site);
						}
					} else {
						self.showModel('查询失败！')
					}
				});
		}
	},
	onLoad(option) {
		// console.log(option)
	},
	mounted() {
		this.getXFPlace();
	}
};
